@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.desktop {
        flex-direction: row;
    }

    &__enlace {
        color: v.$blanco;
        font-size: 2rem;
        padding: 2rem;
        margin: 0;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;
        width: 100%;
        font-size: small(1.6rem, normal, 2rem);
        @include m.tablet{
            width: auto;
            
        }
        @include m.desktop {
            width: auto;
            
        }
        @include m.xl_desktop{
            width: auto;
            
        }

        &--actual,
        &:hover {
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}