@use 'base/variables' as v;
@use 'base/mixins' as m;

.bloques {

    @include m.contenedor;
    @include m.tablet {
        margin-top: 10rem;
    }

    &__grid {
        @include m.grid(1, 3rem);
        @include m.desktop {
            @include m.grid(2, 3rem);
        }
    }

}
.bloqueG {
    padding: 3rem;
    color: v.$blanco;
    border-radius: 1rem;

    &:nth-child(1){
        @include m.bg-gradient(#ae0227e4);
    }
    &:nth-child(2){
        @include m.bg-gradient(#000000);
    }
    &:nth-child(3){
        @include m.bg-gradient((#000000));
    }
    &:nth-child(4){
        @include m.bg-gradient(#ae0227e4);
    }
    &:nth-child(5){
        @include m.bg-gradient(#ae0227e4);
    }
    &:nth-child(6){
        @include m.bg-gradient(#656262);
    }
    &:nth-child(7){
        @include m.bg-gradient(#656262);
    }
    &:nth-child(8){
        @include m.bg-gradient(#ae0227e4);
    }

    &:nth-child(9){
        @include m.bg-gradient(#ffffff);
        color: #656262;
    }
    &:nth-child(10){
        color: #656262;
        @include m.bg-gradient(#ffffff);
    }
    &:nth-child(11){
        color: #656262;
        @include m.bg-gradient(#ffffff);
        
    }
    &:nth-child(12){
        color: #656262;
        @include m.bg-gradient(#ffffff);
        
    }
    

    &__heading {
        margin: 1rem 0 2rem 0;
        font-size: 3.6rem;
        
    }

    &__texto {
        margin: 1rem 0;
        font-size: 2rem;
        
        &--cantidad {
            margin-top: 4rem;
            font-weight: v.$black;
            font-size: 3rem;
            text-align: center;
            @include m.telefono {
                margin: 3rem;
                text-align: center;
            }
            &-total {
                font-size: 8rem;
                text-align: center;
            }
        }
        &-meta {
            text-align: center; 
            font-weight: v.$black;
            font-size: 2.2rem;
            color: #656262;
            margin: 20px solid;
            padding: 2rem;
            border-radius: 10px;


            &-lista {
                background-color: rgb(65, 239, 7);
            }
            &-media {
                background-color: yellow;
                
            }
            &-apunto {
                background-color: rgb(183, 255, 49);
                
            }
            &-baja {
                background-color: red;
                color: #000000;
            }
        }
    }
}

.table__td {
    color: #000000;
}

.bloqueBoletos {

    &__pase {
        padding: 1rem;
        display: flex;
        text-align: center;
        justify-content:center;
        &--tipo {
            
        }
        &--titulo {
            margin-top: 20px;
            font-size: 3.3rem;
            padding: 0 6rem;
        }
        &--cantidad {
            margin: 0;
            font-size: 4rem;
        }
    }
}
